.sectionsContainer {
  width: 100%;
  height: 352px;
  margin-bottom: 20px;
}

.picCanvas {
  width: 329px;
  height: 352px;
  background-color: grey;
  display: inline-block;
  margin-right: 50px;
}

.contentCanvas {
  width: 329px;
  height: 352px;
  display: inline-block;
  vertical-align: top;
}

.titleBox {
  width: 300px;
  height: 81px;
  border-bottom: 1px solid #3A3A3A;
  margin: 0 auto;
  margin-top: 30px;
  margin-bottom: 35px;
}

.typeTitle {
  color: #3A3A3A;
  font-size: 50px;
}

.itemText {
  font-family: 'Shree Devanagari 714';
  color: #3A3A3A;
  font-size: 31px;
}



