.icons {
  width: 120px;
  height: 40px;
  float: right;
  padding-right: 142px;
}

.iconsMoved{
  width: 120px;
  height: 40px;
  float: right;
  padding-right: 142px;
  padding-top: 50px;
}
.MenuIcon {
  display: inline-block;
  width: 46px;
  height: 49px;

}

.BasketIcon {
  display: inline-block;
  width: 55px;
  height: 41px;
  padding-bottom: 4px;
}

.menuContainer {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  z-index: 1;
  position: relative;
}

.Logo {
  margin: 0 auto;
  width: 70px;
  height: 100px;
  z-index: 0;
}

nav {
  width: 900px;
  max-height: 177px;
  margin: 0 auto;
  padding-top: 42px;
}

a:hover {
  color: #EBD382;
}

.linkTitle {
  font-size: 26px;
  font-family: 'Shree Devanagari 714';
  font-weight: 100;
  color: white;
  text-decoration: none;
  text-align: center;
  padding-left: 80px;
}



